import React, { useRef, useEffect, useState } from 'react';
import { Card, Form, Input, InputNumber, Button, Image, Upload, Row, Col, Select } from 'antd';
import { Table, Badge, Menu, Dropdown, Space } from 'antd';
import { DownOutlined } from '@ant-design/icons';
import GallerySection from '../GallerySection';
import { Editor } from '@tinymce/tinymce-react';
import { createPlace, getPlaceDetail, searchPlaces, getAllPlacesWithTitle, updatePlaceDetail } from 'api/api-place';

const { Option } = Select;

const menu = (
    <Menu>
      <Menu.Item>Action 1</Menu.Item>
      <Menu.Item>Action 2</Menu.Item>
    </Menu>
);

const PlaceForm = ({match}:any) => {

    const placeId = match.params.placeId;
    const editorRef = useRef<any>(null);
    const secondEditorRef = useRef<any>(null);
    const [allPlaces, setAllPlaces] = useState<any>([]);
    const [searchParentTitle, setSearchParentTitle] = useState<any>([]);
    const [placeContent, setPlaceContent] = useState<string>('');
    const [secondPlaceContent, setSecondPlaceContent] = useState<string>('');
    const [isRequesting, setIsRequesting] = useState(false);
    const [gallery, setGallery] = useState<any[]>([]);
    // const log = () => {
    //     if (editorRef.current) {
    //     console.log(editorRef.current.getContent());
    //     }
    // };
    const [form] = Form.useForm();
    console.log('placeId ===>', match.params.placeId);

    useEffect(()=>{
        getAllPlacesWithTitle(match.params.placeId).then(data=>{
            setAllPlaces(data.body);
            if(parseInt(match.params.placeId) > 0){
                getPlaceDetail(match.params.placeId).then(data=>{
                    console.log('getPlaceDetail ===>', data.body);
                    form.setFieldsValue(data.body);
                    setPlaceContent(data.body.content);
                    setGallery(data.body.gallery);
                    setSecondPlaceContent(data.body.second_page_content);
                });
            }
        });
    }, []);

    const onFinish = async (value:any) => {
        if (editorRef.current) {
            value.content = editorRef.current.getContent();
            value.second_page_content = secondEditorRef.current.getContent();
        }
        setIsRequesting(true);
        if(parseInt(placeId) > 0){
            await updatePlaceDetail(placeId, value);
        }else{
            await createPlace(value);
        }
        setIsRequesting(false);
    };


    const handleSearch = (value:any) => {
        console.log('handleSearch --->', value);
        setSearchParentTitle(value);
    };

    

    const options = allPlaces.filter((ele:any)=>ele.title.includes(searchParentTitle)).map((d:any) => <Option key={d.id} value={`${d.id}`}>{d.title}</Option>);

    return (
        <>
        <Card title = "Page Info">
            <Form form={form} style={{ marginTop: 20 }} onFinish={onFinish}>
              Place title:
              <Form.Item
                name="title"
                rules={[{ required: true, message: 'required!' }]}
              >
                <Input />
              </Form.Item>

                Search Label:
				<Form.Item
					name="search_label"
					rules={[{ required: true, message: 'required!' }]}
				>
					<Input />
				</Form.Item>

                Parent Place:
                <Form.Item
					name="place_parent"
					rules={[{ required: true, message: 'required!' }]}
				>
                    <Select
                        showSearch
                        defaultActiveFirstOption={false}
                        showArrow={false}
                        filterOption={false}
                        style = {{width:'100%'}}
                        onSearch={handleSearch}
                        // onChange={onChangeParentVal}
                        // notFoundContent={null}
                    >
                        {options}
                    </Select>
                </Form.Item>
                Longitude:
				<Form.Item
					name="longitude"
					rules={[{ required: true, message: 'required!' }]}
				>
					<Input />
				</Form.Item>

                Latitude:
				<Form.Item
					name="latitude"
					rules={[{ required: true, message: 'required!' }]}
				>
					<Input />
				</Form.Item>

                Bounds:
				<Form.Item
					name="bounds"
					rules={[{ required: true, message: 'required!' }]}
				>
					<Input />
				</Form.Item>

                List Label:
				<Form.Item
					name="list_label"
					rules={[{ required: true, message: 'required!' }]}
				>
					<Input.TextArea />
				</Form.Item>
                
                Content:
                <Editor
                    apiKey = "n16h33nt1xigk2hha9alkvvgxqyqa48akfey3cg9c6xdxxrc"
                    onInit={(evt, editor) => {
                        editorRef.current = editor;
                    }}
                    initialValue = {placeContent}
                    init={{
                        height: 500,
                        menubar: false,
                        plugins: [
                            'advlist autolink lists link image charmap print preview anchor',
                            'searchreplace visualblocks code fullscreen',
                            'insertdatetime media table paste code help wordcount'
                        ],
                        toolbar: 'undo redo | formatselect | ' + ' link image |' +
                        'bold italic backcolor | alignleft aligncenter ' +
                        'alignright alignjustify | bullist numlist outdent indent | ' +
                        'removeformat | help',
                        content_style: 'body { font-family:Helvetica,Arial,sans-serif; font-size:14px }'
                }}/>
                <br/>
                Second Content:
                <Editor
                    apiKey = "n16h33nt1xigk2hha9alkvvgxqyqa48akfey3cg9c6xdxxrc"
                    onInit={(evt, editor) => {
                        secondEditorRef.current = editor;
                    }}
                    initialValue = {secondPlaceContent}
                    init={{
                        height: 500,
                        menubar: false,
                        plugins: [
                            'advlist autolink lists link image charmap print preview anchor',
                            'searchreplace visualblocks code fullscreen',
                            'insertdatetime media table paste code help wordcount'
                        ],
                        toolbar: 'undo redo | formatselect | ' + ' link image |' +
                        'bold italic backcolor | alignleft aligncenter ' +
                        'alignright alignjustify | bullist numlist outdent indent | ' +
                        'removeformat | help',
                        content_style: 'body { font-family:Helvetica,Arial,sans-serif; font-size:14px }'
                }}/>
                <br/>
                <Form.Item>
                  <Button type="primary" htmlType="submit" loading={isRequesting}>
                    Publish
                  </Button>
                </Form.Item>
            </Form>
        </Card>
        <br/>
        <GallerySection gallery = {gallery} />
        </>
    );
};

export default PlaceForm; 