import React, { useRef, useEffect, useState } from 'react';
import { Modal, Button, Row, Col, Tabs, Input, Image, Form, Upload } from 'antd';
import { CheckSquareFilled, MinusSquareFilled, InboxOutlined, FileImageOutlined} from '@ant-design/icons';
import { createPhoto } from 'api/api-photo';
import config from 'api/config';
import classes from './style.module.scss';

interface Props {
    onFinishedUpload:(ele:any)=>void;
}

export const UploadForm = ({ onFinishedUpload }:Props) => {

    const [file, setFile] = useState<any>({});

    const props = {
        name: 'file',
        multiple: true,
        action: `${config.host}/api/photos/upload`,
        headers: {
			Authorization: `Bearer ${localStorage.getItem('token')}`,
		},
        showUploadList:false,
        onChange(info:any) {
          const { status } = info.file;
          console.log('info.file.percent ---->', info.file.percent);
        //   if (status === 'uploading') {
        //     console.log(info.file, info.fileList);
        //   }
        //   if (status === 'done') {
        //     console.log(`${info.file.name} file uploaded successfully.`);
        //   } else if (status === 'error') {
        //     console.log(`${info.file.name} file upload failed.`);
        //   }
            // setActiveTab('1');
            setFile(info.file);
        },
        onDrop(e:any) {
          console.log('Dropped files', e.dataTransfer.files);
        },
    };

    const [form] = Form.useForm();

    const onFinish = (values:any) => {
        values.fileName = file.response.fileName;
        console.log('values ===>', values);
        createPhoto(values).then((res:any)=>{
            console.log('res ====>', res.body);
            onFinishedUpload(res.body.photo);
        }).catch((error:any)=>{
            console.log(error.message);
        });
    };

    console.log('file --->', file);

    let fileLink;
    try {
        fileLink = file.response.fileLink;
    }catch(error){
        console.log('error ==>', error.message);
    }


    return (
        <>
            <Row>
                <div className={classes.uploadBox}>
                    <Upload.Dragger {...props}>
                        <div>
                            <p className="ant-upload-drag-icon">
                                <InboxOutlined />
                            </p>
                            <p className="ant-upload-text">Click or drag file to this area to upload</p>
                        </div>
                    </Upload.Dragger>
                </div>
            </Row>
            <br />
            <Row>
                <Col span={12}>
                    <div>
                        {fileLink && <Image src = {fileLink} style = {{width:'100%'}}/>}
                    </div>
                </Col>
                <Col span={12}>
                    <div style = {{padding:8}}>
                        <Form form = {form} labelCol = {{span:24}} wrapperCol = {{span:24}} onFinish = {onFinish}>
                            <Form.Item label="Alt:" name = "alt">
                                <Input />
                            </Form.Item>
                            <Form.Item label="Description:" name = "description">
                                <Input.TextArea />
                            </Form.Item>
                            <Form.Item>
                                <Button style = {{marginRight:0, marginLeft:'auto'}} type = "primary" htmlType ="submit">Save</Button>
                            </Form.Item>
                        </Form>
                    </div>
                </Col>
            </Row>
        </>
    );
};